import React, { useEffect, useState } from 'react';
import {
  Button,
  Container,
  SeeMore,
  Title,
  TopLessons,
} from './Popularlasses.styled';
import sprite from '../../assets/sprite.svg';
import { dataTopLessons } from './backend_temp';

export const Popularlasses = () => {
  const [ data, setData ] = useState( [] );
  const [ isSeeMore, setIsSeeMore ] = useState( true );

  useEffect( () => {
    if ( isSeeMore ) {
      const res = [];

      for ( let i = 0; i < 18; i += 1 ) {
        res.push( dataTopLessons[ i ] );
      }

      return setData( res );
    }

    setData( dataTopLessons );
  }, [ isSeeMore ] );

  const addSeeMore = () => {
    setIsSeeMore( prev => !prev );
  };
  return (
    <Container>
      <Title> </Title>
      <TopLessons>
        {data.map( ( { id, nameLesson, numTeach } ) => (
          <li key={ id }>
            <h2>{nameLesson}</h2>
            <div>
              <p>
{numTeach}
{' '}

</p>

              <Button
                onClick={ () => {
                  console.log( 'click on Item lesson' );
                } }
              >
                <svg width="24px" height="24px">
                  <use href={ `${sprite}#icon-arrow` }></use>
                </svg>
              </Button>
            </div>
          </li>
        ) )}
      </TopLessons>

      <SeeMore>
        {isSeeMore ? <p> </p> : <p> </p>}

        <Button $isActive={ isSeeMore } onClick={ addSeeMore }>
          <svg width="24px" height="24px">
            <use href={ `${sprite}#icon-arow_down` }></use>
          </svg>
        </Button>
      </SeeMore>
    </Container>
  );
};
