import React from 'react';
import logo from '../../../assets/home/logo.svg';

import * as S from './ModalThanksForJoining.staled';
import { BtnRegistration } from '../../Hero/modals/BtnRegistration';
import { bgColorGradientBtn, white } from '../../../utils/variables.styled';

export const ModalThanksForJoining = ( { onActiveModal } ) => {
  return (
    <S.Modal>
      <S.Article>
        <S.LogoImg src={ logo } alt="HUB23" />
        <S.Title>,  </S.Title>
        <S.TextDescr>
            ,    
        </S.TextDescr>

        <BtnRegistration
          xlMarginBottom="30px"
          mdMarginBottom="40px"
          smMarginBottom="30px"
          color={ white }
          xlHeight="60px"
          smHeight="45px"
          bgColorGradient={ bgColorGradientBtn }
          onRegister={ () => {
            onActiveModal();
            console.log( ' E-mail' );
          } }
        >
          <S.BtnText> E-mail</S.BtnText>
        </BtnRegistration>

        <S.Text>
           ,     24 .   
            Hub23,  ,   
        </S.Text>
      </S.Article>
    </S.Modal>
  );
};
